	export default {
		data() {
			return {
				user_type_range: ['管理员', '用户'],
				user_type_index: 0,
				
				sex_range: ['男', '女'],
				sex_index: 0,
				
				agree: false,
			}
		},
		
		methods: {
			userTypeChange(e) {
				this.user_type_index = e.detail.value
			},
			
			sexChange(e) {
				this.sex_index = e.detail.value
			},
			
			agreeChange(e) {
				this.agree = !this.agree
				console.log(this.agree)
			},

			submit_form() {
				uni.showToast({
					icon: 'none',
          title: '该手机号未注册',
          duration: 2000
        })
			},
			
			goIndex() {
				uni.redirectTo({
					url: '/src/pages/index/index'
				})
			}
		}
	}
